.section-banner {
  background: #25644d url(images/graphics/bg-banner.jpg) no-repeat top;
  background-size: cover;
  margin-top: -70px;
  color: #fff;
}
.section-banner .grid-home {
  align-items: center;
}
.section-banner .banner-mobile {
  display: none;
}
.section-banner .content {
  display: flex;
  flex-wrap: wrap;
  column-gap: 20px;
  row-gap: 20px;
  align-content: start;
}
.section-banner .content * {
  margin-bottom: 0;
}
.section-banner .title {
  font-size: 48px;
  font-weight: 500;
  width: 100%;
  line-height: 1.3;
}
.section-banner .des {
  width: 100%;
  font-size: 18px;
}
.section-banner .price {
  width: 100%;
  display: flex;
  column-gap: 20px;
  row-gap: 20px;
  align-items: center;
}
.section-banner .price .price-old {
  font-size: 20px;
  text-decoration-line: line-through;
  color: #F9A825;
}
.section-banner .price .price-new {
  font-size: 32px;
  font-weight: 500;
  color: #F9A825;
}
.section-banner .img {
  text-align: right;
  margin-bottom: -60px;
}
@media (max-width: 1480px) {
  .section-banner .title {
    font-size: 36px;
  }
}
@media (max-width: 1280px) {
  .section-banner .title {
    font-size: 32px;
  }
}
@media (max-width: 1025px) {
  .section-banner {
    margin-bottom: 20px;
  }
  .section-banner .content {
    column-gap: 10px;
    row-gap: 10px;
  }
  .section-banner .title {
    font-size: 26px;
  }
  .section-banner .des {
    font-size: 16px;
  }
  .section-banner .price .price-old {
    font-size: 16px;
  }
  .section-banner .price .price-new {
    font-size: 24px;
  }
  .section-banner .img {
    margin-bottom: -20px;
  }
}
@media (max-width: 600px) {
  .section-banner {
    margin-top: 0;
    padding: 0;
  }
  .section-banner .grid-home {
    display: none;
    gap: 10px;
  }
  .section-banner .banner-mobile {
    display: block;
  }
  .section-banner .title {
    font-size: 22px;
  }
  .section-banner .des {
    font-size: 14px;
  }
  .section-banner .price {
    column-gap: 10px;
    row-gap: 10px;
  }
  .section-banner .price .price-old {
    font-size: 14px;
  }
  .section-banner .price .price-new {
    font-size: 20px;
  }
  .section-banner .img {
    width: 150px;
    flex-shrink: 0;
  }
}

.section-sukien {
  margin-top: -120px;
  margin-bottom: 30px;
  position: relative;
  z-index: 1;
}
.section-sukien .grid {
  column-gap: 30px;
  row-gap: 30px;
}
.section-sukien .title {
  font-size: 16px;
  color: #fff;
}
.section-sukien .article-itemt.full {
  text-align: center;
  gap: 8px;
}
.section-sukien .article-itemt.full .thumb_img {
  border-radius: 12px;
  border: 2px solid #F1F8F5;
}
.section-sukien .article-itemt.full .thumb_img img {
  -webkit-transition: -webkit-transform 0.3s ease-out, opacity 0.35s;
  -webkit-transition: opacity 0.35s, -webkit-transform 0.3s ease-out;
  transition: opacity 0.35s, -webkit-transform 0.3s ease-out;
  transition: transform 0.3s ease-out, opacity 0.35s;
  transition: transform 0.3s ease-out, opacity 0.35s, -webkit-transform 0.3s ease-out;
  backface-visibility: hidden;
}
.section-sukien .article-itemt.full .thumb_img:hover img {
  -webkit-transform: scale(1.2);
  -ms-transform: scale(1.2);
  transform: scale(1.2);
  -webkit-transition: -webkit-transform 4s ease-out;
  transition: -webkit-transform 4s ease-out;
  transition: transform 4s ease-out;
  transition: transform 4s ease-out, -webkit-transform 4s ease-out;
}
.section-sukien .article-itemt.full .article-content {
  justify-content: center;
  gap: 4px;
}
@media (max-width: 1480px) {
  .section-sukien .grid__5 {
    grid-template-columns: repeat(4, 1fr);
  }
  .section-sukien .grid__5 .article-itemt:nth-child(5) {
    display: none;
  }
}
@media (max-width: 1025px) {
  .section-sukien {
    margin-top: 0px;
  }
  .section-sukien .grid {
    column-gap: 10px;
    row-gap: 10px;
  }
  .section-sukien .title {
    color: #00110b;
  }
}
@media (max-width: 600px) {
  .section-sukien {
    margin-bottom: 15px;
  }
  .section-sukien .title {
    text-transform: uppercase;
    font-weight: 700;
    color: #034C31;
    text-align: center;
    margin-bottom: 15px;
  }
  .section-sukien .title span {
    color: #FF5E57;
  }
  .section-sukien .grid {
    margin-right: -15px;
    padding-right: 15px;
    width: calc(100% + 15px);
    column-gap: 15px;
    row-gap: 15px;
    scroll-behavior: smooth;
    -ms-overflow-style: -ms-autohiding-scrollbar;
    overflow-x: auto;
    -webkit-overflow-scrolling: touch;
    -webkit-scroll-snap-type: x mandatory;
    -ms-scroll-snap-type: x mandatory;
    scroll-snap-type: x mandatory;
    white-space: nowrap;
    font-size: 0;
  }
  .section-sukien .grid::-webkit-scrollbar {
    display: none;
  }
  .section-sukien .grid .article-itemt {
    width: 172px;
    scroll-snap-align: start;
  }
}

.head-course {
  margin-bottom: 30px;
  display: flex;
  align-items: end;
  gap: 60px;
  position: relative;
}
.head-course::before {
  content: "";
  position: absolute;
  background-color: var(--bg-main);
  height: 3px;
  left: 0;
  right: 380px;
  bottom: 0;
}
.head-course__left {
  width: 100%;
}
.head-course__right {
  width: 320px;
  flex-shrink: 0;
}
.head-course .tab-default {
  border-bottom: 0;
}
@media (max-width: 1480px) {
  .head-course {
    gap: 20px;
  }
  .head-course::before {
    right: 0;
    bottom: -6px;
  }
  .head-course .tab-default a.active::before {
    bottom: -6px;
  }
}
@media (max-width: 1025px) {
  .head-course {
    flex-wrap: wrap;
    gap: 10px;
  }
  .head-course__right {
    width: 100%;
  }
  .head-course .tab-default {
    border-bottom: 3px solid var(--bg-main);
  }
  .head-course .tab-default a.active::before {
    bottom: -3px;
  }
  .head-course::before {
    display: none;
  }
}
@media (max-width: 600px) {
  .head-course {
    margin-bottom: 15px;
  }
  .head-course .line-tab {
    position: relative;
  }
  .head-course .line-tab::before {
    content: "";
    background: #fff;
    position: absolute;
    width: 100%;
    height: 3px;
    bottom: 3px;
  }
  .head-course .tab-default {
    border-bottom: 0;
    padding-bottom: 3px;
  }
  .head-course .tab-default a.active::before {
    bottom: 0;
  }
}

.box-grid-time {
  column-gap: 10px;
  row-gap: 10px;
  border-bottom: 3px solid #F1F8F5;
  margin-bottom: 30px;
  padding-bottom: 30px;
}
.box-grid-time .box-item {
  border-radius: 4px;
  background: #F1F8F5;
  padding: 10px;
}
.box-grid-time .box-item .title {
  font-size: 18px;
  margin-bottom: 4px;
  font-weight: 400;
  margin-top: 3px;
}
.box-grid-time .box-item .time {
  font-weight: 300;
}
.box-grid-time .box-time {
  display: flex;
  align-items: center;
  gap: 20px;
  justify-content: end;
}
@media (max-width: 1480px) {
  .box-grid-time {
    display: flex;
  }
  .box-grid-time .box-item {
    width: 100%;
  }
  .box-grid-time .box-item .title {
    font-size: 16px;
  }
  .box-grid-time .follow-us {
    width: 395px;
    flex-shrink: 0;
  }
}
@media (max-width: 1025px) {
  .box-grid-time {
    margin-bottom: 20px;
    padding-bottom: 20px;
  }
  .box-grid-time .follow-us {
    display: none;
  }
}
@media (max-width: 600px) {
  .box-grid-time {
    border-bottom: 0;
    padding-bottom: 0;
    margin-bottom: 15px;
  }
  .box-grid-time .box-time {
    border-radius: 8px;
    border: 1px solid #dbe2df;
    background: #fefefe url(images/icons/cup.png) no-repeat top -13px left -8px;
    gap: 12px;
    color: var(--colormain);
    padding: 3px;
  }
  .box-grid-time .box-time .text-right {
    text-align: center !important;
  }
  .box-grid-time .box-time .title {
    font-size: 14px;
    font-weight: 700;
    padding: 0 15px;
    line-height: 1.6;
  }
  .box-grid-time .box-time .time {
    display: none;
  }
  .box-grid-time .box-time .countdown {
    border-radius: 8px;
    background: var(--bg-main);
    gap: 15px;
    padding: 4px 6px;
  }
}

.follow-us {
  display: flex;
  align-items: center;
  gap: 10px;
}
.follow-us .list-user {
  display: flex;
  align-items: center;
}
.follow-us .list-user .ava {
  position: relative;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  overflow: hidden;
  margin-left: -12px;
}
.follow-us .list-user .ava:nth-child(1) {
  margin-left: 0px;
  z-index: 5;
}
.follow-us .list-user .ava:nth-child(2) {
  z-index: 4;
}
.follow-us .list-user .ava:nth-child(3) {
  z-index: 3;
}
.follow-us .list-user .ava:nth-child(4) {
  z-index: 2;
}
.follow-us .list-user .ava:nth-child(5) {
  z-index: 1;
}
.follow-us .des {
  font-size: 16px;
}
.follow-us .des a {
  color: var(--colormain);
}
@media (max-width: 1480px) {
  .follow-us .des {
    font-size: 14px;
  }
}